function fetchData() {

  const url = `https://api.odcloud.kr/api/15070340/v1/uddi:4682264b-e754-4aa9-a0d7-b3dcd0f746b7?page=1 &perPage=3000&serviceKey=%2BeKIiP9KuILfRQMYW6mwid1FuD6OBJvjLh02%2FwqpUdnFfeK6KePliqFYtnqxnSQALgi0pHrlQPK6XD%2BXh2tfyg%3D%3D`;

  const promise = fetch(url)
    .then(res => {
      if (!res.ok) {
        throw res;
      }
      return res.json();
    })

  return promise;
}

